<template>
 <div>
    <div class="d-block header-section">
		<div class="py-3 align-self-center d-sm-flex w-100 rounded">
			<div class="w-sm-100 mr-auto">
				<h1>
				<b>Fulfill Stock Request</b>
				</h1>
			</div>
			
		</div>
    </div>
    <div class="container-fluid productManagement">
      <!-- START: Card Data-->
		
        <div class="card mb-4">
            <div class="row p-5">
                <div class="col-12 col-md-4 p-3 row">
                    <div class="col-md-3"><img src="@/assets/images/icons/order-two.png"/></div>
                    <div class="col-md-9 mt-2">
                    <h5 class="text-muted">Order Status</h5>
                    <h5 class="d-flex align-items-left text-primary">{{orderStatus}}</h5>
                    </div>
                </div>
                <div class="col-12 col-md-4 border-left p-3 row">
                    <div class="col-md-3"><img src="@/assets/images/icons/order-three.png"/></div>
                    <div class="col-md-9 mt-2">
                    <h5 class="text-muted">Warehouse Name</h5>
                    <h5 class="d-flex align-items-left text-primary">{{warehouseName}}</h5>
                    </div>
                </div>
                <div class="col-12 col-md-4 border-left p-3 row">
                    <h5 class="d-flex align-items-left text-primary"> </h5>
                </div>
            </div>
        </div>
        <div class="container-fluid productManagement">
            <template>
                <div class="mb-4">
                    <b-table :items="items" :fields="fields" responsive="sm" class="itemListingFulfill">
                        <template v-slot:cell(actions)="row">
                            <b-button size="sm" variant="primary" @click="toggleDetails(row.index)" class="mr-2">
                                Fulfill
                            </b-button>
                        </template>

                        <template v-slot:row-details="row">
                            <div class="row pb-3">
                                <div class="row col-md-4">
                                    <label class="col-md-3 pt-2">Package</label>
                                    <select class="form-control col-md-9" @change="onProductChange(row.item.id)" v-model="selectedPackage" name="Package">
                                        <option disabled selected value="0">-select-</option>
                                        <option
                                            v-for="item in packageList"
                                            :value="{ id: item.productId, text: item.productName }"
                                            :key="item.productId"
                                        >
                                            {{ item.productName }}
                                        </option>
                                    </select>
                                </div>
                                <div class="row col-md-4">
                                    <label class="col-md-3 pt-2" for="codeValue">Code</label>
                                    <input
                                        class="form-control col-md-9"
                                        id="codeValue"
                                        v-model="codeValue"
                                        :disabled="!selectedPackage || showCartonSelection"
                                    />
                                </div>
                                <div class="row col-md-4 ml-2">
                                    <button class="btn btn-primary" :disabled="!codeValue || showCartonSelection" @click="fetchCoils(row.item.id,row.index)">Add</button>
                                </div>
                            </div>
                            <b-card v-if="showCartonSelection">
                                <h6>Please select the real codes from the Carton</h6>
                                <div>
                                    <b-form-group class="mb-0">
                                        <template v-slot:label>
                                            <b-form-checkbox
                                            v-model="selectAll"
                                            :indeterminate="indeterminate"
                                            aria-describedby="codes"
                                            aria-controls="codes"
                                            @change="toggleAll"
                                            >
                                             {{ selectAll ? 'Deselect All' : 'Select All' }}
                                            </b-form-checkbox>
                                        </template>
                                        <b-form-checkbox-group
                                            id="codes"
                                            v-model="selectedCodes"
                                            :options="codes"
                                            name="codes"
                                            class="mb-3"
                                        ></b-form-checkbox-group>
                                    </b-form-group>
                                </div>
                                <div class="d-flex flex-row-reverse">
                                    <b-button class="pull-right ml-3" size="sm" variant="primary" @click="cartonSelection(row.item.id,row.index)" :disabled="selectedCodes.length<=0">ADD</b-button>
                                    <b-button class="pull-right" size="sm" variant="primary" @click="clearCartonSelection">CANCEL</b-button>
                                </div>
                            </b-card>
                            <b-card v-if="row.item.coilListing.length > 0">
                                <table id="tab" class="table itemListingFulfill coilListing">
                                    <thead>
                                        <tr>
                                            <th>Package</th>
                                            <th>Code</th>
                                            <th>No. of Stamps</th>
                                            <th>Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, index) in row.item.coilListing" :key="index">
                                            <td>{{ packageObj[item.packageType] }}</td>
                                            <td>{{ item.code }}</td>
                                            <td>{{ item.stampCount }}</td>
                                            <td class="remove-x-btn text-center"><span @click="removeItem(row.index,index)"><b>X</b></span></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td><b>Total</b></td>
                                            <td>{{row.item.totalAddedCoils}}</td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td><b>Coils required</b></td>
                                            <td>{{row.item.totalCoils}}</td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td><b>Balance Coils required</b></td>
                                            <td>{{row.item.balanceCoils}}</td>
                                        </tr>
                                    </tbody>
                                </table>
                                <div class="d-flex flex-row-reverse">
                                    <b-button class="pull-right" size="sm" variant="primary" @click="toggleDetails(row.index)">CANCEL</b-button>
                                </div>
                            </b-card>
                        </template>
                    </b-table>
                </div>
            </template>
            <div class="card mb-2 pb-3">
                <div class="row">
                    <div class="col-md-4">
                        <p class="mt-4 col-md-12">Tracking ID</p>
                        <div class="col-md-12 pr-0">
                            <input
                                class="form-control"
                                v-model="trackingID"
                            />
                        </div>
                    </div>
                    <div class="col-md-4">
                        <p class="mt-4 col-md-12">Shipper</p>
                        <div class="col-md-12 pr-0">
                            <p>{{warehouseName}}</p>
                        </div>
                    </div>
                    <div class="col-md-4">
                        <p class="mt-4 col-md-12"><label for="expectedDate" class="mb-0 text-dark">Expected Date</label></p>
                        <div class="form-group row mt-3 col-md-12 pr-0">
                            <div class="col-md-12 pr-0">
                                <b-input-group>
                                    <b-form-input
                                        id="expectedDate"
                                        v-model="expectedDate"
                                        type="text"
                                        autocomplete="off"
                                    ></b-form-input>
                                    <b-input-group-append>
                                        <b-form-datepicker
                                        v-model="expectedDate"
                                        button-only
                                        right
                                        locale="en-US"
                                        button-variant="primary"
                                        ></b-form-datepicker>
                                    </b-input-group-append>
                                </b-input-group>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-12">
                    <p class="mt-2">Notes</p>
                    <div>
                        <textarea class="form-control" rows="5" v-model="notes"></textarea>
                    </div>
                </div>
            </div>
            <div class="mb-2">
                <b-button v-b-modal.modal-sm
                    class="float-right mt-2 mb-3 p-2 pl-5 pr-5"
                    variant="primary"
                    :disabled="!(trackingID && expectedDate && notes && fulfillmentBool)"
                    @click="postFulfillOrder"
                >
                    SUBMIT
                </b-button>
                <b-button v-b-modal.modal-sm
                    class="float-right mt-2 mb-3 mr-2 p-2 pl-5 pr-5"
                    variant="outline-primary"
                    @click="redirectToHome"
                >
                    CANCEL
                </b-button>
            </div>
        </div>
    </div>
  </div>

</template>

<script>
import axios from "axios";
import toast from '@/toast';
export default {

    data: () => ({
        orderStatus: "",
        warehouseName: "",
        trackingID: "",
        editFlow: false,
        fulfillmentBool: false,
        showCartonSelection: false,
        selectedPackage: {},
        packageList: [],
        packageObj: {},
        selectedFacilityLocation: "",
        expectedDate: null,
        notes: "",
        codeValue: "",
        selectAll: false,
        indeterminate: false,
        selectedCodes: [],
        codes: [],
        selectedRowIndex: 0,
        selectedCarton: {},
        facilityLocationList: [],
        fields: ['product', "total_stamps",'actions'],
        items: []
    }),
    props:['requestId'],
    mounted() {
        this.getWarehouses();
        this.getPackageTypes();
        if(this.requestId) {
            this.editFlow = true;
            axios.get("/InternalStock", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                params: {requestId:this.requestId},
            }).then((response) => {
                this.items = [
                    {
                        _showDetails: false,
                        isActive: true,
                        id: response.data.id,
                        productTypeID: response.data.productId,
                        product: response.data.productName,
                        total_stamps: response.data.noOfStamps,
                        totalAddedCoils: 0,
                        totalCoils: response.data.noOfStamps,
                        balanceCoils: response.data.noOfStamps,
                        coilListing: [],
                        CartonCoilObject: {}
                    }
                ]
                this.selectedFacilityLocation = {id: response.data.requestingFacilityId,text: response.data.requestingFacilityName};
                this.warehouseName = response.data.requestingFacilityName;
                this.orderStatus = response.data.status;
                this.notes = response.data.notes;
            }).catch(err => {
                if (err.response) {
                    toast.warning(err.response.data);
                } else if (err.request) {
                    console.log("err 2",err.request);
                } else {
                    console.log("err 3",err);
                }
            });
        } else {
            this.editFlow = false;
        }
    },
    computed: {
    },
    methods: {
        updateFulfillmentBool: function () {
            let booleanCdn = true;
            if(this.items.length>0)
                this.fulfillmentBool = booleanCdn;
        },
        postFulfillOrder() {
            let reqbody = {
                requestId: this.requestId,
                packages: this.items[0].coilListing,
                trackingId: this.trackingID,
                shipperId: parseInt(this.selectedFacilityLocation.id),
                expectedDate: this.expectedDate,
                comments: this.notes
            }
            axios.post("/InternalStock/SaveInternalStockTransfer",
                reqbody,
                {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                },
            ).then(() => {
                this.$router.push({ name: 'StockRequestList' });
            }).catch(err => {
                if (err.response) {
                    toast.warning(err.response.data);
                } else if (err.request) {
                    console.log("err 2",err.request);
                } else {
                    console.log("err 3",err);
                }
            });
        },
        fetchCoils(id,index) {
            let tempCoilListing = [...this.items[index].coilListing];
            let obj = tempCoilListing.find(o => o.code == this.codeValue);
            if(obj === undefined) {
                let coilArray = [];
                if(this.selectedPackage.text == "Carton") {
                    axios.get("/InternalStock/GetStockTransferCarton", {
                        baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                        params: {cartonCode:this.codeValue,requestId:this.requestId}
                    }).then((response) => {
                        if(!(response.data.id in this.items[index].CartonCoilObject)) {
                            this.showCartonSelection = true;
                            if(response.data.reels.length > 0) {
                                this.selectedCarton = response.data;
                                response.data.reels.map((reel)=>{
                                    this.codes.push(reel.code);
                                });
                            }
                        } else {
                            toast.warning("Some Coils in this Carton is already added in the table");
                            this.selectedPackage = "";
                            this.clearCartonSelection();
                        }
                    }).catch(err => {
                        if (err.response) {
                            toast.warning(err.response.data);
                        } else if (err.request) {
                            console.log("err 2",err.request);
                        } else {
                            console.log("err 3",err);
                        }
                    });
                    this.selectAll = false;
                } else {
                    this.showCartonSelection = false;
                    axios.get("/InternalStock/GetStockTransferReel", {
                        baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                        params: {reelCode:this.codeValue,requestId:this.requestId}
                    }).then((response) => {
                        if(!(response.data.cartonId in this.items[index].CartonCoilObject)) {
                            let coil = {
                                cartonId: response.data.cartonId,
                                code: response.data.code,
                                id: response.data.id,
                                isUsed: response.data.isUsed,
                                packageType: response.data.packageType,
                                reelSize: response.data.reelSize,
                                stampCount: response.data.stampCount
                            }
                            coilArray.push(coil);
                            this.addCoil(coilArray,id,index);
                        } else {
                            toast.warning("Carton which contains this coil is already added in the table");
                            this.selectedPackage = "";
                            this.clearCartonSelection();
                        }
                    }).catch(err => {
                        if (err.response) {
                            toast.warning(err.response.data);
                        } else if (err.request) {
                            console.log("err 2",err.request);
                        } else {
                            console.log("err 3",err);
                        }
                    });
                }
            } else {
                let msg = this.selectedPackage.text+" "+this.codeValue+" is already added in the table";
                toast.warning(msg);
                this.selectedPackage = "";
                this.clearCartonSelection();
            }
        },
        addCoil(coilArray, id,index) {
            let tempCoilListing = [...this.items[index].coilListing];
            if(!(coilArray[0].cartonId in this.items[index].CartonCoilObject)) {
                this.items[index].CartonCoilObject[coilArray[0].cartonId] = [];
            }
            if(this.selectedPackage.text == "Reel") {
                let tempCoil = {
                    packageId: coilArray[0].id,
                    code: this.codeValue,
                    stampCount: coilArray[0].stampCount,
                    packageType: parseInt(this.selectedPackage.id)
                };
                tempCoilListing.push(tempCoil);
                this.updateTotalCoils(tempCoilListing,index);
            } else {
                if(this.selectAll) {
                    let totalStamp = coilArray.reduce(function (total, count) {
                        return total + parseInt(count.stampCount); // returns object with property x
                    },0);
                    let tempCoil = {
                        packageId: coilArray[0].cartonId,
                        code: this.codeValue,
                        stampCount: totalStamp,
                        packageType: parseInt(this.selectedPackage.id)
                    };
                    tempCoilListing.push(tempCoil);
                    this.updateTotalCoils(tempCoilListing,index);
                } else {
                    coilArray.map((coil) => {
                        let tempCoil = {
                            packageId: coil.id,
                            code: coil.code,
                            stampCount: coil.stampCount,
                            packageType: parseInt(coil.packageType)
                        };
                        tempCoilListing.push(tempCoil);
                    });
                    this.updateTotalCoils(tempCoilListing,index);
                }
            }
            this.selectedPackage = "";
            this.clearCartonSelection();
        },
        calculateTotalCoils(coilArray,tempCoilListing,index) {
            let totalAddedCoils = coilArray.reduce(function (total, count) {
                return total + parseInt(count.stampCount); // returns object with property x
            },0);
            let remainingCoils = this.items[index].balanceCoils - totalAddedCoils;
            if(remainingCoils >= 0) {
                return true;
            }
            else {
                return false;
            }
        },
        updateTotalCoils(tempCoilListing,index) {
            let totalCoils = tempCoilListing.reduce(function (total, count) {
                return total + parseInt(count.stampCount); // returns object with property x
            },0);
            this.items[index].coilListing = [...tempCoilListing];
            this.items[index].totalAddedCoils = totalCoils;
            this.items[index].balanceCoils = this.items[index].totalCoils - totalCoils;
            this.updateFulfillmentBool();
        },
        onProductChange() {
            this.clearCartonSelection();
        },
        toggleAll(checked) {
            this.selectedCodes = checked ? this.codes.slice() : []
        },
        toggleDetails(index) {
            if(this.selectedRowIndex == index) {
                this.items[index]._showDetails = !this.items[index]._showDetails;
            }
            else {
                this.selectedRowIndex = index;
                this.items.map((item) => {
                    item._showDetails = false;
                });
                this.items[index]._showDetails = true;
            }
            this.selectedPackage = "";
            this.clearCartonSelection();
        },
        cartonSelection(id,index) {
            let coilArray = [];
            if(this.selectAll) {
                // this.addCoil(id,index);
                this.selectedCarton.reels.map((reel)=>{
                    let coil = {
                        cartonId: reel.cartonId,
                        code: reel.code,
                        id: reel.id,
                        isUsed: reel.isUsed,
                        packageType: reel.packageType,
                        reelSize: reel.reelSize,
                        stampCount: reel.stampCount
                    }
                    coilArray.push(coil);
                });
                this.addCoil(coilArray,id,index);
            } else {
                this.selectedCodes.map((code) => {
                    this.codeValue = code;
                    this.selectedCarton.reels.map((reel)=>{
                        if(this.codeValue == reel.code) {
                            let coil = {
                                cartonId: reel.cartonId,
                                code: reel.code,
                                id: reel.id,
                                isUsed: reel.isUsed,
                                packageType: reel.packageType,
                                reelSize: reel.reelSize,
                                stampCount: reel.stampCount
                            }
                            coilArray.push(coil);
                        }
                    });
                });
                this.addCoil(coilArray,id,index);
            }
            this.selectedPackage = "";
            this.clearCartonSelection();
        },
        clearCartonSelection() {
            this.codeValue = "";
            this.selectAll = false;
            this.indeterminate = false;
            this.selectedCodes = [];
            this.codes = [];
            this.showCartonSelection = false;
        },
        removeItem(tableIndex, rowIndex) {
            console.log("this.items[tableIndex].coilListing before", this.items[tableIndex].coilListing);
            var coilCode = this.items[tableIndex].coilListing[rowIndex].code;
            if(this.packageObj[this.items[tableIndex].coilListing[rowIndex].packageType] == "Reel") {
                for (var key in this.items[tableIndex].CartonCoilObject) {
                    if (this.items[tableIndex].CartonCoilObject[key].includes(coilCode)) {
                        this.items[tableIndex].CartonCoilObject[key].splice(this.items[tableIndex].CartonCoilObject[key].indexOf(coilCode),1);
                        if(this.items[tableIndex].CartonCoilObject[key].length == 0) {
                            delete this.items[tableIndex].CartonCoilObject[key];
                        }
                    }
                }
            } else if (this.packageObj[this.items[tableIndex].coilListing[rowIndex].packageType] == "Carton") {
                delete this.items[tableIndex].CartonCoilObject[this.items[tableIndex].coilListing[rowIndex].packageId];
            }
            this.items[tableIndex].coilListing.splice(rowIndex,1);
            console.log("this.items[tableIndex].coilListing after", this.items[tableIndex].coilListing);
            this.updateTotalCoils(this.items[tableIndex].coilListing,tableIndex);
        },
        redirectToHome() {
            this.$router.push({ name: 'StockRequestList' });
        },
        getWarehouses() {
            axios.get("/Meta/GetWarehouses", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
            }).then((response) => {
                var tempWarehouses = [];
                for (let key in response.data) {
                    tempWarehouses.push({'productId': key, 'productName': response.data[key]});
                }
                this.facilityLocationList = tempWarehouses;
            }).catch(err => {
                if (err.response) {
                    toast.warning(err.response.data);
                } else if (err.request) {
                    console.log("err 2",err.request);
                } else {
                    console.log("err 3",err);
                }
            });
        },
        getPackageTypes() {
            axios.get("/Packages/GetPackageTypes", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }).then((response) => {
                var packageTypes = [];
                for (let key in response.data) {
                    if(response.data[key] !== "Pallet")
                        packageTypes.push({'productId': key, 'productName': response.data[key]});
                }
                this.packageList = packageTypes;
                this.packageObj = response.data;
            }).catch(err => {
                if (err.response) {
                    toast.warning(err.response.data);
                } else if (err.request) {
                    console.log("err 2",err.request);
                } else {
                    console.log("err 3",err);
                }
            });
        },
    },
    watch: {
      selectedCodes(newVal) {
        // Handle changes in individual flavour checkboxes
        if (newVal.length === 0) {
          this.indeterminate = false
          this.selectAll = false
        } else if (newVal.length === this.codes.length) {
          this.indeterminate = false
          this.selectAll = true
        } else {
          this.indeterminate = true
          this.selectAll = false
        }
      }
    }

};
</script>
